* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

#datetime-picker {
  height: 40px;
  color: #212121;
  text-align: center;
  background-color: #fff;
  border: 2px solid #ccc;
  border-radius: 4px;
  padding: .5rem 1rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  text-decoration: none;
  transition: all .2s ease-in-out;
}

button {
  height: 40px;
  color: #212121;
  cursor: pointer;
  text-align: center;
  background-color: #fff;
  border: 2px solid #ccc;
  border-radius: 4px;
  padding: .5rem 1rem;
  font-size: 1rem;
  font-weight: 400;
  text-decoration: none;
  transition: all .2s ease-in-out;
}

.timer {
  flex-wrap: nowrap;
  align-items: center;
  display: flex;
}

.field {
  flex-direction: column;
  margin-left: 25px;
  display: flex;
}

.value {
  font-size: 40px;
  font-weight: 400px;
}

.label {
  text-transform: uppercase;
  font-size: .75rem;
  font-weight: 700;
}

